@import url(https://fonts.googleapis.com/css2?family=Lato&family=Montserrat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  font-family: "Montserrat", sans-serif;
  background: black;
  color: white;
}
p {
  font-family: "Lato", sans-serif;
}

.menu-wrap{position:fixed;top:0;left:0;z-index:100}.menu-wrap .toggler{position:absolute;top:0;left:0;z-index:2;cursor:pointer;width:60px;height:60px;opacity:0;margin:16px}.menu-wrap .hamburger{position:absolute;top:0;left:0;z-index:1;width:60px;height:60px;padding:1rem;background:var(--primary-color);display:flex;align-items:center;justify-content:center}.menu-wrap .hamburger>div{position:relative;flex:none;width:100%;height:2px;background:#fff;display:flex;align-items:center;justify-content:center;transition:all 0.4s ease}.menu-wrap .hamburger>div::before,.menu-wrap .hamburger>div::after{content:"";position:absolute;z-index:1;top:-10px;width:100%;height:2px;background:inherit}.menu-wrap .hamburger>div::after{top:10px}.menu-wrap .toggler:checked+.hamburger>div{transform:rotate(135deg)}.menu-wrap .toggler:checked+.hamburger>div:before,.menu-wrap .toggler:checked+.hamburger>div:after{top:0;transform:rotate(90deg)}.menu-wrap .toggler:checked:hover+.hamburger>div{transform:rotate(225deg)}.menu-wrap .toggler:checked ~ .menu{visibility:visible}.menu-wrap .toggler:checked ~ .menu>div{transform:scale(1);transition-duration:var(--menu-speed)}.menu-wrap .toggler:checked ~ .menu>div>div{opacity:1;transition:opacity 0.4s ease 0.4s}.menu-wrap .menu{position:fixed;top:0;left:0;width:100%;height:100%;visibility:hidden;overflow:hidden;display:flex;align-items:center;justify-content:center}.menu-wrap .menu>div{background:black;border-radius:50%;width:200vw;height:200vw;display:flex;flex:none;align-items:center;justify-content:center;transform:scale(0);transition:all 0.4s ease}.menu-wrap .menu>div>div{text-align:center;max-width:90vw;max-height:100vh;opacity:0;transition:opacity 0.4s ease}.menu-wrap .menu>div>div>ul>li{list-style:none;color:#fff;font-size:1.5rem;padding:1rem}.menu-wrap .menu>div>div>ul>li>a{color:inherit;text-decoration:none;transition:color 0.4s ease}.mid-title{position:fixed;width:100vw;display:flex;justify-content:center;padding-top:16px}@media screen and (max-width: 500px){.mid-title svg{width:200px;padding:10px 0}.menu-wrap .menu>div{height:300vh}}

main{background:linear-gradient(180deg, rgba(0,0,0,0) 0%, #000 100%),url(/fourier/static/media/background.ad8d068b.jpg);height:100vh;display:flex;justify-content:center;align-items:center;position:fixed;width:100vw;height:100vh;z-index:0}main h1{padding:1rem;font-size:3rem}.buffer{height:100vh;z-index:5}

.card{width:600px;height:400px;display:flex}.card img{margin:40px}.card .overlay{opacity:0.5}.card h3{position:absolute;font-size:2rem;margin-left:20px;color:white;z-index:3;width:500px}.card .summary{display:flex;position:absolute;z-index:2;width:600px;visibility:hidden;font-size:1.5rem;background-color:black;opacity:0%;align-content:space-between;align-items:center;overflow:hidden;height:400px;transition:ease opacity 0.5s}.card .summary p{padding:2rem;opacity:0%;color:white}.card .summary a{float:right;margin-right:50px;text-decoration:none;color:#9e00ff}.card:hover .summary{visibility:visible;opacity:75%}.card:hover .summary p{opacity:100%}@media screen and (max-width: 600px){.card{width:300px;height:200px}.card img{width:240px;height:auto}.card .summary{width:300px;height:200px}.card .summary p{font-size:1rem}.card .summary a{font-size:1rem}.card h3{width:240px;font-size:1rem}}

.card-list{display:grid;grid-template-columns:auto auto;z-index:3}.card-list .card:nth-child(4n+1){background-color:#131179}.card-list .card:nth-child(4n+2){background-color:#971f84}.card-list .card:nth-child(4n+3){background-color:#971f84}.card-list .card:nth-child(4n+4){background-color:#131179}@media only screen and (max-width: 1250px){.card-list{grid-template-columns:auto}.card-list .card:nth-child(odd){background-color:#131179}.card-list .card:nth-child(even){background-color:#971f84}}

.container{display:flex;justify-content:center;width:100%;background-color:black;z-index:2}

